body {
  background-color: #424242;
  font-family: Lato, helvetica, arial, sans-serif;
  -webkit-font-smoothing: antialiased;
}

.dark {
  color: #fff;
  background-color: #333;
  padding-top: 80px;
  padding-bottom: 50px;
}


p {
  font-size: 17px;
  line-height: 1.6;
}



.img-header {
  height: 350px;

  .lawnmowing & { @include bgimg(lawnmowing_lg) }
  .referral & { @include bgimg(referral)}
  .landscapedesign & { @include bgimg(landscape_banner)}
  .landscapemaintenance & { @include bgimg(lawncare_lg)}
  .mulching & { @include bgimg(mulching_banner)}
  .planting & { @include bgimg(planting_banner)}
  .snowremoval & {@include bgimg(snowremoval_lg)}
  .contact & {@include bgimg(contact)}
}

.landscape_subitem {
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 400px;
  font-size: 30px;
  text-shadow: 0 1px 4px rgba(0,0,0,.8);
  font-weight: bold;

  &.softscape {@include bgimg(softscapeBG)}
  &.hardscape {@include bgimg(hardscapeBG)}
}


.sub-item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 250px;
  margin: 40px 0;

  h3 {
    text-transform: uppercase;
    color: #fff;
    text-align: center;
    font-weight: bold;
    font-size: 26px;
    line-height: 36px;
  }

  &.lawnmaint_bg {@include bgimg(lawnmaint_subBG)}
  &.landscape_bg {@include bgimg(landscape_subBG)}
  &.landscapemaint_bg {@include bgimg(landscapemaint_subBG)}
}

@media screen and (max-width: 480px){
 .sub-item {
   margin: 40px 0;
 }
}


.landscapemaintenance h5 {margin-top: 30px; margin-bottom: 10px; font-weight: bold;}


a h4 {color: white; text-decoration: none;}

.grecaptcha-badge {
  display: none;
}