

.slide {
  //width: 100%;
 color: #fff;
 min-height: 400px;
 display: flex;
 align-items: center;
 justify-content: center;

 &--lawn-mowing { @include bgimg(lawnmowing_lg); }
 &--landscape { @include bgimg(landscape_lg); }
 &--lawn-care { @include bgimg(lawncare_lg); }
 &--planting { @include bgimg(planting_lg); }
 &--mulching { @include bgimg(mulching_lg); }
 &--snow-removal { @include bgimg(snowremoval_lg); }


}

.slide-content {
  display: flex;
  width: 100%;
  height: 100%;
  min-height: 400px;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  h3 {
    font-size: 66px;
    font-weight: bold;
    text-shadow: rgba(0,0,0,8.498039) 0px 2px 5px;
    background-color: rgba(0,0,0,0.0470588);
  }
}
.slick-next {
  right: 25px;
  z-index: 1000;
}
.slick-prev {
  left: 25px;
  z-index: 1000;
}
