.site-header {
  display: flex;
  width: 100%;
  background-color: #fff;
  padding-top: 20px;
  padding-bottom: 20px;
}

.navbar-brand {
  margin-right: 100px;
}



.nav-link, .dropdown-item{
  font-family: 'Lato', sans-serif;
  text-transform: uppercase;
  font-size: 18px;
  margin-right: 10px;

  color: #2f361a;

  &:hover {
    color: #759036;
  }
}

@media screen and (max-width: 1200px){
  .navbar-brand {
    img {
      width: 200px;
    }
    margin-right: 50px;
  }
  .nav-link, .dropdown-item {
    font-size: 16px;
  }
}
