footer {
  background-color: #222;
  color: #fff;
  padding-top: 50px;
  padding-bottom: 50px;


  h3 {
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 10px;
  }

  p {
    font-size: 14px;
  }

  input[type="text"], input[type="email"]{
    width: 100%;
    margin-bottom: 10px;
    padding: 10px;
    font-size: 14px;
  }

  input[type="submit"]{
    background-color: #96c556;
    color: #fff;
    text-transform: uppercase;
    border: 0;
    padding: .6em 1.2em;
    font-size: 16px;
  }

  textarea {
    max-width: 100%;
    padding: 10px;
  }

  ul { padding: 0;}
  li {
    list-style: none;

    a { color: #fff; text-decoration: none;}
  }
}

.btn-send{
  background-color: #96c556;
  color: #fff;
  text-transform: uppercase;
  border: 0;
  padding: .6em 1.2em;
  font-size: 16px;
}
